import { makeObservable, observable, action } from 'mobx';
import axios from 'axios';
// import servicesDataFromMyJson from "../Services.json"
import servicesDataFromMyJson from "../ServicesPhoto.json"
import timeDataFromMyJson from "../Time.json"

class GetStore {
    // servicesData = {};
    servicesData = servicesDataFromMyJson;
    timeData = timeDataFromMyJson;

    currentTimeData = [...this.timeData];

    constructor() {
        makeObservable(this, {
            servicesData: observable,
            timeData: observable,
            currentTimeData: observable,
            setServicesData: action,
            getServicesData: action,
            setTimeData: action,
            getTimeData: action,
            setCurrentTimeData: action,
            updateCurrentTimeData: action,
            updateOrAddDay: action, 
        }, { deep: true });
    }

    setServicesData = (newData) => {
        this.servicesData = newData;      
    }

    setTimeData = (newData) => {
        this.timeData = newData;
        this.currentTimeData = newData;
    }

    setCurrentTimeData = (newData) => {
        this.currentTimeData = newData;
    }

    updateCurrentTimeData = () => {
        this.currentTimeData = this.timeData;        
    }

    getServicesData = async () => {
        try {            
            const response = await axios.get('http://192.168.31.223:8080/users/BigBallsSoftwareBot/services');
            this.servicesData = (response.data);
            console.log("   ");
        } catch (error) {
            console.error('    ', error);
        }
    }

    getTimeData = async () => {
        try {
            const response = await axios.get('http://192.168.31.223:8080/users/BigBallsSoftwareBot/services');
            this.timeData = (response.data);
            this.currentTimeData = (response.data);
            console.log("   ");
        } catch (error) {
            console.error('    ', error);
        }

    }

    updateOrAddDay = (newDay) => {
        const index = this.currentTimeData.findIndex(d => d.date === newDay.date);
        let updatedTimeData;
    
        if (index > -1) {
            //   ,     
            updatedTimeData = this.currentTimeData.map((item, idx) => {
                if (idx === index) {
                    return newDay; //   
                }
                return item; //   
            });            
        } else {
            //   ,   
            updatedTimeData = [...this.currentTimeData, newDay];            
            //  updatedTimeData  
            updatedTimeData.sort((a, b) => 
                new Date(a.date) - new Date(b.date)
            );
        }
    
        //  setCurrentTimeData   
        this.setCurrentTimeData(updatedTimeData);
    }
    
}

const getStore = new GetStore();

export default getStore;
