import { makeObservable, observable, action } from 'mobx';


class FirstTwoPage {
    token = ""
    username = ""
    selfEmployedName = ""
    selfEmployedAddress = ""

    constructor() {
        makeObservable(this, {
            token: observable,
            username: observable,
            selfEmployedName: observable,
            selfEmployedAddress: observable,
            setToken: action,
            setUsername: action,
            setSelfEmployedName: action,
            setSelfEmployedAddress: action
        });
    }

    setToken(value) {
        this.token = value;
    }

    setUsername(value) {
        this.username = value;
    }

    setSelfEmployedName(value) {
        this.selfEmployedName = value;
    }

    setSelfEmployedAddress(value) {
        this.selfEmployedAddress = value;
    }
}

const firstTwoPage = new FirstTwoPage();

export default firstTwoPage;