import cl from './ModalUniversal.module.css'
import { useEffect } from 'react';
import { HeadlineBody, HeadLineSemibold } from '../../../textStyles/TextStyleComponents';

const ModalUniversal = ({
    text,
    setIsVisible,
    onConfirm,
    confirmText,
    cancelText
}) => {
    const tg = window.Telegram.WebApp;

    const backdropClass = tg.colorScheme === 'dark' ? `${cl.backdrop} ${cl.dark}` : `${cl.backdrop} ${cl.light}`;
    const confirmButtonClass = confirmText === '' ? `${cl.button} ${cl.confirmDeleteButton}` : `${cl.button} ${cl.confirmCancelButton}`;

    const onCancel = () => {
        setIsVisible(false);
    }

    const handleBackdropClick = (event) => {
        if (event.currentTarget === event.target) {
            onCancel();
        }
    };

    useEffect(() => {
        //   
        document.body.style.overflow = 'hidden';

        //   
        return () => {
            document.body.style.overflow = 'unset';
        };
    }, []);

    return (
        <div className={backdropClass} onClick={handleBackdropClick}>
            <div className={cl.container}>
                <div className={cl.textContainer}>
                    <HeadLineSemibold style={{ textAlign: "center", whiteSpace: 'pre-line' }}>{text}</HeadLineSemibold>
                </div>

                <div className={cl.buttonContainer}>
                    {onConfirm ?
                        <div className={cl.doubleButtonContainer}>
                            <div className={confirmButtonClass} onClick={onConfirm}><HeadLineSemibold>{confirmText}</HeadLineSemibold></div>
                            <div className={cl.border} />
                            <div className={`${cl.button} ${cl.confirmCancelButton}`} onClick={onCancel}><HeadlineBody>{cancelText}</HeadlineBody></div>
                        </div>
                        :
                        <div className={cl.singleButtonContainer} onClick={onCancel}>
                            <HeadlineBody>
                                {cancelText}
                            </HeadlineBody>
                        </div>
                    }
                </div>
            </div>
        </div>
    )
}

export default ModalUniversal