import React from 'react';
import cl from './PhotoUploadButton.module.css';
import { ReactComponent as UploadPhotoIcon } from "../../../../icons/UploadPhoto.svg";

const PhotoUploadButton = ({body, setServicePhotos}) => {
    const fileInputRef = React.createRef();

    const handleFileInput = (e) => {
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                const base64String = reader.result
                    .replace('data:', '')
                    .replace(/^.+,/, '');
                setServicePhotos(prevPhotos => [...prevPhotos, base64String]);
            };
            reader.readAsDataURL(file);
        }
    };

    const handleClick = () => {
        fileInputRef.current.click();
    };

    return (
        <div>
            <input
                type="file"
                ref={fileInputRef}
                style={{display: 'none'}}
                accept="image/*"
                onChange={handleFileInput}
            />
            <button className={cl.btn} onClick={handleClick}>
                {body}
                <UploadPhotoIcon/>
            </button>
        </div>
    );
};

export default PhotoUploadButton;